.wrapRoulette{
  width:400px;
  margin: auto;
  height: 400px;
  position: relative;
  border:1px solid #000;
  margin-top: 20px;
  .rouCol{
      box-sizing: border-box;
      width:25%;
      position: absolute;
      left:0;
      top:0;
      height:100px;
      border:1px solid #000;
      text-align: center;
      line-height: 100px;
  }
  .rouCol2{
      left:25%;
  }
  .rouCol3{
      left:50%;
  }
  .rouCol4{
      left:75%;
  }
  .rouCol5{
      left:75%;
      top:100px;
  }
  .rouCol6{
      left:75%;
      top:200px;
  }
  .rouCol7{
      left:75%;
      top:300px;
  }
  .rouCol8{
      left:50%;
      top:300px;
  }
  .rouCol9{
      left:25%;
      top:300px;
  }
  .rouCol10{
      left:0;
      top:300px;
  }
  .rouCol11{
      left:0;
      top:200px;
  }
  .rouCol12{
      left:0;
      top:100px;
  }
  .btn{
      position: absolute;
      width:100px;
      height:100px;
      border-radius: 50%;
      // height:100px;
      top:0;
      right:0;
      left:0;
      bottom:0;
      margin:auto;
      background-color: rgb(224, 62, 62);
      text-align: center;
      line-height: 100px;
      cursor: pointer;
      border:none;
      border-bottom: 5px solid rgb(184, 25, 25);
      outline:none;
      &:active{
          border-bottom: 1px solid rgb(184, 25, 25);
          box-shadow: 2px 2px 2px #777;
      }
  }
}